import { createRouter, createWebHistory } from 'vue-router'
import AuthView from '@/components/Auth.vue';
import DashboardView from '@/components/Dashboard.vue';
import HTTP403ForbiddenView  from '@/components/Other/403.vue';

const routes = [
  {
    path: '/',
    name: 'auth',
    component: AuthView
  },
  {
    path: '/dashboard',
    name: 'dashboard',
    component: DashboardView
  },
  {
    path: '/error/403',
    name: '403',
    component: HTTP403ForbiddenView
  },
]

const router = createRouter({
  mode: 'history',
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  const allow_routes = ['auth', 'dashboard', '403'];
  if (!allow_routes.includes(to.name)) next({ name: 'auth' })
  next()
});

export default router
