<template>
  <!-- <LoaderView /> -->

  <v-app-bar>
    <v-app-bar-title>Angry Liza: blocker</v-app-bar-title>
    <template v-slot:append>
      <v-btn @click.prevent="logout">Разлогінитись</v-btn>
    </template>
  </v-app-bar>

  <div class="container my-2">
    <div class="pt-2 pb-3 text-center bg-body-tertiary rounded-3">
      <div class="mb-3 p-0">
        👋🏼
      </div>
      <p class="text-body-emphasis text-center fw-bold fs-5 m-0 mb-1">Вітаємо, {{ user.email }}!</p>
      <div class="col-lg-8 mx-auto fs-10 text-muted mb-3">
        <p class="p-0 m-0">
          Використай ID, вказаний нижче, для активації розширення: 
        </p>
        <span class="fw-bold text-center w-100 text-decoration-underline">{{ user.account_id }}</span>
      </div>
      <div class="d-flex align-center justify-center">
        <a class="btn btn-primary btn-sm p-2 text-decoration-none mx-1" download :href="file" target="_blank">
          Завантажити розширення
        </a>
        <a class="btn btn-outline-secondary btn-sm p-2 text-decoration-none mx-1" href="" target="_blank">
          Передивитись туторіал
        </a>
      </div>
    </div>
  </div>

  <div class="container actions">
    <v-btn color="black" @click.prevent="showForm" :loading="loading">
      <v-icon>mdi-arrow-down-bold-box</v-icon>
      <v-tooltip
        activator="parent"
        location="end"
      >
        Дії з блокуватором
      </v-tooltip>
    </v-btn>

    <div v-if="form" class="form mt-2">
      <div class="mb-2">
        <div class="my-1">
          <v-btn size="small" color="primary" @click.prevent="exceptions" :loading="loading">Передевитись виключення</v-btn>
        </div>

        <div class="my-1">
          <v-btn size="small" color="primary" @click.prevent="alert" :loading="loading">Передевитись алерт</v-btn>
        </div>

        <div class="my-1">
          <v-btn size="small" color="primary" @click.prevent="download_history" :loading="loading">Завантажити історію відвудувань</v-btn>
        </div>

        <div class="my-1">
          <v-btn size="small" color="primary" @click.prevent="clear_history" :loading="loading">Почитисти історію відвідувань</v-btn>
        </div>

        <div class="my-1">
          <v-btn size="small" color="primary" @click.prevent="start_pages" :loading="loading">Сторінки для запуску</v-btn>
        </div>
      </div>
      
      <form class="d-flex align-center justify-start">
        <v-text-field
          class="col-md-4"
          v-model="inputs.exception.upload"
        ></v-text-field>
        <v-btn class="mx-3" color="primary" @click.prevent="upload_exception" :loading="loading">Додати виключення</v-btn>
      </form>

      <form class="d-flex align-center justify-start">
        <v-text-field
          class="col-md-4"
          v-model="inputs.alert"
        ></v-text-field>
        <v-btn class="mx-3" color="primary" @click.prevent="edit_alert" :loading="loading">Редагувати алерт</v-btn>
      </form>

      <form class="d-flex align-center justify-start">
        <v-text-field
          class="col-md-4"
          v-model="pages.href"
        ></v-text-field>
        <v-btn class="mx-3" color="primary" @click.prevent="add_start_page" :loading="loading">Додати стартову сторінку</v-btn>
      </form>
    </div>  
  </div>

  <div v-if="dialog" class="modal">
    <v-dialog v-model="dialog" width="300">
      <v-card>

        <v-card-text v-if="pages.hrefs.length === 0 && pages.blocks.length === 0" class="text-center">
          {{ request.result }}
        </v-card-text>

        <v-table v-if="pages.hrefs.length !== 0 && pages.blocks.length === 0" height="300px">
          <thead>
            <tr>
              <th class="text-center">
                Посилання
              </th>
              <th class="text-center">
                Дії
              </th>
            </tr>
          </thead>
          <tbody class="text-center">
            <tr
              v-for="item in pages.hrefs"
              :key="item.id"
            >
              <td>{{ item.href.length < 20 ? item.href : item.href.substr(0, 17) + '...' }}
                <v-tooltip
                  activator="parent"
                  location="start"
                >{{ item.href }}</v-tooltip>
              </td>
              <td>
                <v-btn @click="delete_start_page(item.href)">🗑
                  <v-tooltip
                    activator="parent"
                    location="end"
                  >Видалити {{ item.href }}</v-tooltip>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>

        <v-table v-if="pages.hrefs.length === 0 && pages.blocks.length !== 0" height="300px">
          <thead>
            <tr>
              <th class="text-center">
                Посилання
              </th>
              <th class="text-center">
                Дії
              </th>
            </tr>
          </thead>
          <tbody class="text-center">
            <tr
              v-for="item in pages.blocks"
              :key="item.id"
            >
              <td>{{ item.length < 20 ? item : item.substr(0, 17) + '...' }}
                <v-tooltip
                  activator="parent"
                  location="start"
                >{{ item }}</v-tooltip>
              </td>
              <td>
                <v-btn @click="delete_exception(item)">🗑
                  <v-tooltip
                    activator="parent"
                    location="end"
                  >Видалити {{ item }}</v-tooltip>
                </v-btn>
              </td>
            </tr>
          </tbody>
        </v-table>

        <v-card-actions>
          <v-btn color="primary" block @click="dialogClose">Закрити</v-btn>
        </v-card-actions>

      </v-card>
    </v-dialog>
  </div>

  <!-- <HtaccessView /> -->
 </template>

<script>
import File from '@/assets/dist.zip';
import ifUserAuth from '@/mixins/api.js';
import alert_func from '@/mixins/api.js';
import exceptions_func from '@/mixins/api.js';
import upload_exception_func from '@/mixins/api.js';
import delete_exception_func from '@/mixins/api.js';
import edit_alert_func from '@/mixins/api.js';
import clear_history_func from '@/mixins/api.js';
import start_pages_func from '@/mixins/api.js';
import add_start_page_func from '@/mixins/api.js';
import delete_start_page_func from '@/mixins/api.js';
// import HtaccessView from '@/components/Other/htaccess.vue';
// import LoaderView from '@/components/Other/loader.vue';

export default {
  name: 'DashboardView',
  mixins: [ifUserAuth, alert_func, exceptions_func, upload_exception_func, delete_exception_func, edit_alert_func, clear_history_func, start_pages_func, add_start_page_func, delete_start_page_func],
  // components: { HtaccessView, LoaderView },
  created() {
    this.ifUserAuth();

    var userData = localStorage.getItem('user');
    if(userData !== null) {
      userData = JSON.parse(userData);
      this.user = userData;
      this.account_id = userData.account_id;
    } else {
      this.$router.push({name: "auth"})
    }
  },
  data() {
    return {
      blockerExtensionAPI: this.$store.getters.extension,
      file: File,
      user: null,
      account_id: null,
      form: false,
      dialog: false,
      loading: false,
      inputs: {
        exception: {
          upload: '',
          delete: ''
        },
        alert: ''
      },
      request: {
        result: ''
      },
      pages: {
        hrefs: [],
        blocks: [],
        href: ''
      }
    }
  },
  methods: {
    dialogClose() {
      this.pages.hrefs = [];
      this.pages.blocks = [];
      this.dialog = false;
      return true;
    },
    logout() {
      localStorage.removeItem('user');
      this.ifUserAuth();
    },
    showForm() {
      const form = this.form;
      if(form === false) {
        this.form = true;
        var scroll = setTimeout(() => {
            document.querySelector('#app').scrollTo({
            top: document.querySelector('#app').scrollHeight,
            left: 0,
            behavior: 'smooth'
          });
        }, 1)

        return scroll;
      }

      this.inputs.exception.upload = '';
      this.inputs.exception.delete = '';
      this.inputs.alert = '';
      this.pages.href = '';
      this.pages.hrefs = [];

      return this.form = false;
    },
    async alert() {
      try {
        this.loading = true;
        const request = await this.alert_func(this.account_id);
        if(request.status === 200) {
          this.dialog = true;
          this.request.result = request.message;
        } else {
          alert(request.message);
        }
      } catch (error) {
        console.error(error)
        alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
      }

      this.loading = false;
      return true;
    },
    async download_history() {
      var url = `${this.blockerExtensionAPI}/download_history/${this.account_id}`;
      return window.open(url);
    },
    async clear_history() {
      try {
        this.loading = true;
        const request = await this.clear_history_func(this.account_id);
        if(request.status === 200) {
          this.dialog = true;
          this.request.result = request.message;
        } else {
          alert(request.message);
        }
      } catch (error) {
        console.error(error)
        alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
      }

      this.loading = false;
      return true;
    },

    async start_pages() {
      try {
        this.loading = true;
        const request = await this.start_pages_func(this.account_id);
        if(request.status === 200) {
          this.dialog = true;
          if(request.message.length === 0 || (request.message.length === 1 && request.message[0] === '')) {
            this.request.result = 'Cторінки для запуску відсутні!';
          } else {
            this.pages.hrefs = request.message;
          }
        } else {
          alert(request.message);
        }
      } catch (error) {
        console.error(error)
        alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
      }

      this.loading = false;
      return true;
    },

    async add_start_page() {
      var href = this.pages.href.replaceAll(' ', '');

      if(this.isValidUrl(href)) {
        try {
          this.loading = true;
          this.dialog = false;

          const request = await this.add_start_page_func(this.account_id, href);
          if(request.status === 200) {
            this.pages.href = '';
            this.pages.hrefs = [];
            await this.start_pages();
          } else {
            alert(request.message);
          }
        } catch (error) {
          console.error(error)
          alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
        }
      } else {
        if(href.length === 0) {
          alert('Не залишайте полe пустим!');
        } else {
          alert('Можливо завантажити лише http та https посилання!');
        }
      }

      this.loading = false;
      return true;
    },

    isValidUrl(url) {
      var pattern = /^(https?|ftp):\/\/[^\s/$.?#].[^\s]*$/i;
      return pattern.test(url);
    },

    async delete_start_page(href) {
      try {
          this.loading = true;
          this.dialog = false;

          const request = await this.delete_start_page_func(this.account_id, href);
          if(request.status === 200) {
            this.pages.hrefs = [];
            await this.start_pages();
          } else {
            alert(request.message);
          }
        } catch (error) {
          console.error(error)
          alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
        }

      this.loading = false;
      return true;
    },

    async exceptions() {
      try {
        this.loading = true;
        const request = await this.exceptions_func(this.account_id);
        if(request.status === 200) {
          this.dialog = true;
          if(request.message.length === 0 || (request.message.length === 1 && request.message[0] === '')) {
            this.request.result = 'Cторінки для блокування відсутні!';
          } else {

            this.pages.blocks = request.message.filter(page => page.replaceAll(' ', '').length !== 0);
          }
        } else {
          alert(request.message);
        }
      } catch (error) {
        console.error(error)
        alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
      }

      this.loading = false;
      return true;
    },
    async upload_exception() {
      var input = this.inputs.exception.upload;
      if(input.replaceAll(' ', '').length !== 0) {
        try {
          this.loading = true;
          this.dialog = false;

          const request = await this.upload_exception_func(this.account_id, input);
          if(request.status === 200) {
            this.inputs.exception.upload = '';
            this.pages.blocks = [];
            await this.exceptions();
          } else {
            alert(request.message);
          }
        } catch (error) {
          console.error(error)
          alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
        }
      } else {
        alert('Не залишайте полe пустим!');
      }

      this.loading = false;
      return true;
    },
    async delete_exception(input) {
      try {
        this.loading = true;
        this.dialog = false;

        const request = await this.delete_exception_func(this.account_id, input);
        if(request.status === 200) {
          this.pages.blocks = [];
          await this.exceptions();
        } else {
          alert(request.message);
        }
      } catch (error) {
        console.error(error)
        alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
      }

      this.loading = false;
      return true;
    },
    async edit_alert() {
      if(this.inputs.alert.replaceAll(' ', '').length !== 0) {
        try {
          this.loading = true
          const request = await this.edit_alert_func(this.account_id, this.inputs.alert);
          if(request.status === 200) {
            this.inputs.alert = '';
            this.dialog = true;
            this.request.result = request.message;
          } else {
            alert(request.message);
          }
        } catch (error) {
          console.error(error)
          alert("Упс... щось трапилось. Спробуйте ще раз, або перевірте інтернет-з'єднання!")
        }
      } else {
        alert('Не залишайте полe пустим!');
      }

      this.loading = false;
      return true;
    }
  }
}
</script>
