<template>
  <!-- <LoaderView /> -->
  
  <v-container style="height: 100vh; width: 100%;">
    <div class="w-100 h-100 d-flex align-center justify-center">
      <div class="w-100 shadow p-4 bg-body-tertiary rounded">
        <div class="w-100 d-flex align-center justify-center mb-4">
          <img :src="logo" width="100" />
        </div>

        <div class="w-100 d-flex align-center justify-center mb-2">
          <v-text-field
            class="col-md-4"
            v-model="auth.email.value"
            :rules="auth.email.rules"
            label="Пошта"
          ></v-text-field>
        </div>

        <div class="w-100 d-flex align-center justify-center mb-2">
          <v-text-field
            class="col-md-4"
            v-model="auth.password.value"
            :rules="auth.password.rules"
            label="Пароль"
            type="password"
          ></v-text-field>
        </div>

        <div class="w-100 d-flex align-center justify-center mt-2 mb-1">
          <v-btn
            color="black"
            class="m-1"
            type="submit"
            :loading="auth.loading"
            :disabled="auth.disabled"
            @click.prevent="authSubmit('login')"
          >
            Увійти
          </v-btn>
          <v-btn
            color="black"
            class="m-1"
            type="submit"
            :loading="registration.loading"
            :disabled="registration.disabled"
            @click.prevent="authSubmit('registr')"
          >
            Зареєструватися
          </v-btn>
        </div>
      </div>
    </div>
  </v-container>

  <!-- <HtaccessView /> -->
 </template>

<script>
import Logo from '@/assets/logo.png';
import authLaunch from '@/mixins/api.js';
import ifUserAuth from '@/mixins/api.js';
// import HtaccessView from '@/components/Other/htaccess.vue';
// import LoaderView from '@/components/Other/loader.vue';

export default {
  name: 'AuthView',
  mixins: [authLaunch, ifUserAuth],
  // components: { HtaccessView, LoaderView },
  data() {
    return {
      logo: Logo,
      auth: {
        loading: false,
        disabled: false,
        email: {
          value: '',
          rules:  [ 
            () => {
              if (this.ifEmail(this.auth.email.value)) {
                this.auth.email.error = false;
                return true
              }
              return 'Введіть коректну пошту!'
          }],
          error: true
        },
        password: {
          value: '',
          rules: [
            () => {
              if (this.auth.password.value.length > 7) {
                this.auth.password.error = false;
                return true
              }
              return 'Пароль повинен бути не менш 8 символів!'
          }],
          error: true
        }
      },
      registration: {
        loading:false,
        disabled: false
      }
    }
  },
  created() {
    this.ifUserAuth()
  },
  methods: {
    ifEmail(email) {
      var regex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
      return regex.test(email);
    },
    downloadCredentials(email, password) {
      var csvText = `Відновлення паролю неможливе, тому збережіть цей файл.\n\nПошта: ${email}\nПароль: ${password}`;
      var result = {
        status: 200
      }

      var blob = new Blob([csvText], { type: 'text/csv;charset=utf-8;' });
      var link = document.createElement("a");
      if (link.download !== undefined) {
          var url = URL.createObjectURL(blob);
          link.setAttribute("href", url);
          link.setAttribute("download", "credentials.csv");
          document.body.appendChild(link);
          link.click();
          document.body.removeChild(link);

          return result;
      } else {
        result['message'] = 'Ваш браузер не підтримує можливість автоматичного завантаження файлів. Реєстрація неможлива!'
        return result
      }
    },
    unBlockButtons() {
      this.auth.disabled = false;
      this.registration.loading = false;
      this.registration.disabled = false;
      this.auth.loading = false;
    },
    async authSubmit(action) {
      try {
        var password = this.auth.password.error;
        var email = this.auth.email.error;

        if(password !== true && email !== true) {
          if(action === 'registr') {
            this.auth.disabled = true;
            this.registration.loading = true;
          } else {
            this.registration.disabled = true;
            this.auth.loading = true;
          }
          

          password = this.auth.password.value;
          email = this.auth.email.value;
          const request = await this.authLaunch(email, password, action);
          if(request.status === 200) {
            if(action === 'registr') {
              const downloadCredentials = await this.downloadCredentials(email, password);
              if(downloadCredentials.status !== 200) {
                alert(downloadCredentials.message);
              } else {
                localStorage.setItem('user', JSON.stringify(request.message));
                return this.$router.push({name: "dashboard"});
              }
            } else {
              localStorage.setItem('user', JSON.stringify(request.message));
              return this.$router.push({name: "dashboard"});
            }
          } else {
            alert(request.message);
          }
        } else {
          alert("Заповніть усі поля форми коректно!")
        }
      } catch (error) {
        console.error(error)
        alert(error.message)
      }

      this.unBlockButtons();
      return true;
    }
  }
}
</script>
